import React from 'react';
import {Icon} from 'react-shared/components/Icon';
import {Heading} from 'react-shared/components/Text';
import {Expando} from 'react-shared/components/Expando';
import styles from './CollapsibleSection.scss';
import cn from 'classnames';
import {VSCodeBadge} from '@vscode/webview-ui-toolkit/react';

type Props = {
  title: string;
  isOpen: boolean;
  onToggle: () => void;
  amountWithLabels: number;
  children: React.ReactNode;
};

export function CollapsibleSection({title, isOpen, onToggle, children, amountWithLabels}: Props) {
  return (
    <section className={cn(styles.collapsibleSection, {[styles.isOpen]: isOpen})}>
      <button onClick={onToggle} className={styles.header}>
        <Icon icon="chevron-down" className={styles.chevronIcon} />
        <Heading className={styles.title} noCursor level={5}>
          {title}
        </Heading>
        <div className={styles.actions}>
          {!isOpen && amountWithLabels > 0 && <VSCodeBadge>{amountWithLabels}</VSCodeBadge>}
        </div>
      </button>
      <Expando classNameContent={styles.content} expanded={isOpen}>{children}</Expando>
    </section>
  );
}
