type ScriptGlobals = {
  vsCodeHelperUrl: string;
};

declare global {
  interface Window extends ScriptGlobals {}
}

export function setupVsCodeHelperIframe(vscodeApi: any) {
  let iframe = document.createElement('iframe');
  iframe.src = window.vsCodeHelperUrl;
  iframe.style.display = 'none';
  document.body.appendChild(iframe);

  window.addEventListener('message', event => {
    let data = JSON.parse(JSON.stringify(event.data));
    if (event.source === iframe.contentWindow) {
      vscodeApi.postMessage(data);
    } else {
      if (event.data.payload === '_rpc-heartbeat') {
        vscodeApi.postMessage(
          {
            requestId: event.data.requestId,
          },
          '*'
        );
      } else {
        iframe.contentWindow!.postMessage(data, '*');
      }
    }
  });

  iframe.onload = function () {
    vscodeApi.postMessage('initialized');
  };
}
