import React, {useEffect, useMemo, useState} from 'react';
import {broker} from '~webviews/mainpanel.entry';
import {Integrations} from '../../../src/main-panel/integrations/utils/integrations-manager';
import {CollapsibleSection} from './CollapsibleSection';
import {UnaddedIntegrationCard} from './IntegrationCards';
import styles from './IntegrationsSection.scss';
import {CloudRunIntegration} from './integrations/CloudRunIntegration';
import {FirebaseIntegration} from './integrations/FirebaseIntegration';
import {GeminiIntegration} from './integrations/GeminiIntegration';
import {GoogleMapsIntegration} from './integrations/GoogleMapsIntegration';

export function IntegrationsSection() {
  const [integrations, setIntegrations] = useState<Integrations | undefined>();

  const addedIntegrations = useMemo(() => {
    return integrations?.filter(i => i.state.added) ?? [];
  }, [integrations]);

  const unaddedIntegrations = useMemo(() => {
    return integrations?.filter(i => !i.state.added) ?? [];
  }, [integrations]);

  const addedLabelsFound = useMemo(
    () => addedIntegrations.reduce((acc, i) => (i.label !== undefined ? ++acc : acc), 0),
    [addedIntegrations]
  );
  const unaddedLabelsFound = useMemo(
    () => unaddedIntegrations.reduce((acc, i) => (i.label !== undefined ? ++acc : acc), 0),
    [addedIntegrations]
  );

  const [isAddedOpen, setIsAddedOpen] = useState(true);
  const [isUnaddedOpen, setIsUnaddedOpen] = useState(false);

  useEffect(() => {
    let firstTime = true;
    broker.send('getWorkspaceIntegrations');
    broker.on('notifyWorkspaceIntegrations', integrations => {
      if (firstTime) {
        if (!integrations.find(i => i.state.added)) {
          setIsUnaddedOpen(true);
        }
      }
      firstTime = false;
      setIntegrations(integrations);
    });
  }, []);

  return (
    <>
      {addedIntegrations.length > 0 && (
        <CollapsibleSection
          isOpen={isAddedOpen}
          onToggle={() => setIsAddedOpen(!isAddedOpen)}
          amountWithLabels={addedLabelsFound}
          title="In this workspace"
        >
          <div className={styles.cardContainer}>
            {addedIntegrations.map(integration => {
              switch (integration.id) {
                case 'gemini_api':
                  return <GeminiIntegration key={integration.id} integration={integration} />;
                case 'google_maps_platform':
                  return <GoogleMapsIntegration key={integration.id} integration={integration} />;
                case 'cloud_run_deploy':
                  return <CloudRunIntegration key={integration.id} integration={integration} />;
                case 'firebase_hosting':
                  return <FirebaseIntegration key={integration.id} integration={integration} />;
              }
            })}
          </div>
        </CollapsibleSection>
      )}
      <CollapsibleSection
        isOpen={isUnaddedOpen}
        onToggle={() => setIsUnaddedOpen(!isUnaddedOpen)}
        amountWithLabels={unaddedLabelsFound}
        title={addedIntegrations.length > 0 ? 'More Integrations' : 'Google Integrations'}
      >
        <div className={styles.cardContainer}>
          {unaddedIntegrations.map(integration => {
            return (
              <UnaddedIntegrationCard
                onIntegrationAdded={() => setIsAddedOpen(true)}
                key={integration.id}
                {...integration}
              />
            );
          })}
        </div>
        <a
          href="#"
          className={styles.link}
          onClick={() =>
            broker.send('openUrl', 'https://developers.google.com/idx/guides/google-integrations')
          }
        >
          Learn more about Google integrations
        </a>
      </CollapsibleSection>
    </>
  );
}
