import React, {useEffect, useRef, useState} from 'react';
import {IconButton, IconButtonProps} from './IconButton';
import {MenuState, PopupMenu} from './PopupMenu';

type MenuIconButtonProps<T> = IconButtonProps & {
  minimizeWidth?: boolean;
  dropdownSide?: 'left' | 'right';
  onClick?: React.MouseEventHandler;
};

export const MenuIconButton: React.FC<MenuIconButtonProps<unknown>> = ({
  minimizeWidth = false,
  dropdownSide = 'right',
  onClick,
  children,
  ...props
}) => {
  const [menuState, setMenuState] = useState<MenuState | null>(null);
  const buttonRef = useRef<HTMLButtonElement | null>(null);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    if (!buttonRef.current || !isOpen) {
      return;
    }

    const buttonRect = buttonRef.current.getBoundingClientRect();

    const oppositeSide = dropdownSide === 'right' ? 'left' : 'right';

    if (dropdownSide === 'left') {
      setMenuState({
        top: buttonRect.bottom + 4,
        right: window.innerWidth - buttonRect[oppositeSide],
      });
    } else {
      setMenuState({
        top: buttonRect.bottom + 4,
        left: buttonRect[oppositeSide],
      });
    }
  }, [isOpen, dropdownSide]);

  return (
    <>
      <IconButton
        ref={buttonRef}
        onClick={ev => {
          setIsOpen(true);
          onClick?.(ev);
        }}
        {...props}
      />
      <PopupMenu
        show={isOpen}
        left={menuState?.left}
        top={menuState?.top || 100}
        right={menuState?.right}
        minimizeWidth={minimizeWidth}
        onClose={() => setIsOpen(false)}
      >
        {children}
      </PopupMenu>
    </>
  );
};
