import React from 'react';
import {Integration} from '~common/messaging/modules/main-panel/integrations';
import {
  CardDivider,
  CardItem,
  CollapsibleIntegrationCard,
  widgetFactory,
} from '../IntegrationCards';

const Widget = widgetFactory('gemini_api');

export function GeminiIntegration({integration}: {integration: Integration<'gemini_api'>}) {
  const {enabled} = integration.state;
  return (
    <CollapsibleIntegrationCard
      id={integration.id}
      iconUrl={integration.icon}
      integrationName={integration.title}
      title={integration.state.projectId}
      defaultExpanded={integration.state.expanded}
    >
      {integration.state.authenticated ? (
        <>
          {enabled && (
            <>
              <CardItem icon="layers-active" text="Gemini API enabled" />
              <CardDivider />
            </>
          )}
          {!enabled && <Widget id="enable_api" {...integration.widgets.enable_api} />}
          <Widget id="get_api_key" {...integration.widgets.get_api_key} />
          {!enabled && <CardDivider />}
          <Widget id="open_docs" {...integration.widgets.open_docs} />
        </>
      ) : (
        <Widget id="run_oauth" {...integration.widgets.run_oauth} />
      )}
    </CollapsibleIntegrationCard>
  );
}
