import cn from 'classnames';
import React, {
  FC,
  HTMLAttributes,
  PropsWithChildren,
  ReactNode,
  useEffect,
  useRef,
  useState,
} from 'react';
import {createPortal} from 'react-dom';
import {Icon, IconName} from './Icon';
import styles from './PopupMenu.scss';

export function useModalRootNode(container = document.body) {
  const [modalRoot] = useState(() => document.createElement('div'));

  useEffect(() => {
    container.appendChild(modalRoot);
    return () => {
      container.removeChild(modalRoot);
    };
  }, []);

  return modalRoot;
}

type PopupMenuProps<T> = PropsWithChildren<
  T &
    HTMLAttributes<HTMLElement> & {
      show: boolean;
      left?: number;
      top?: number;
      right?: number;
      minimizeWidth?: boolean;
      onClose: () => void;
    }
>;

export const PopupMenu: FC<PopupMenuProps<unknown>> = ({
  children,
  className,
  show,
  left,
  top,
  right,
  minimizeWidth = false,
  onClose,
}) => {
  const modalRoot = useModalRootNode();
  const blurTimeout = useRef<number>(0);

  return (
    <>
      {show &&
        createPortal(
          <>
            <div className={styles.scrim} onClick={() => onClose()} />
            <ul
              onKeyDown={ev => ev.code === 'Escape' && onClose()}
              onFocus={() => blurTimeout.current && window.clearTimeout(blurTimeout.current)}
              onBlur={() => {
                blurTimeout.current && window.clearTimeout(blurTimeout.current);
                blurTimeout.current = window.setTimeout(() => onClose(), 100);
              }}
              ref={n => {
                if (!n) return;
                setTimeout(() => {
                  // if something in the menu is already focused, bail
                  if (n.contains(document.activeElement)) {
                    return;
                  }
                  // find the first item that's declared autofocus, or if none have done so,
                  // the first focusable item
                  let initialFocusItem: HTMLButtonElement | null =
                    n.querySelector('button[data-autofocus="true"]:not([disabled])') ||
                    n.querySelector('button:not([disabled])');
                  if (initialFocusItem) {
                    initialFocusItem.focus();
                    return;
                  }
                  // no items are focusable, just focus the menu itself
                  n.tabIndex = 0;
                  n.focus();
                  setTimeout(() => (n.tabIndex = -1));
                });
              }}
              style={{
                left: left === undefined ? 'auto' : left,
                right: right === undefined ? 'auto' : right,
                top,
              }}
              className={cn(className, styles.menu, minimizeWidth && styles.minimizeWidth)}
              onClick={() => onClose()}
            >
              {children}
            </ul>
          </>,
          modalRoot
        )}
    </>
  );
};

/**
 * Helper for storing open state of the popup menu, used in conjunction with useState.
 *
 * Use `MenuState | null` as a type, with the presence of a state indicating the menu is open.
 */
export interface MenuState {
  left?: number;
  top: number;
  right?: number;
}

type MenuItemProps<T> = PropsWithChildren<
  T &
    HTMLAttributes<HTMLElement> & {
      icon?: IconName;
      label?: ReactNode;
      subtitle?: string;
      disabled?: boolean;
      type?: 'checkbox' | 'radio' | 'button';
      isSelected?: boolean;
      autofocus?: boolean;
      textAlign?: 'left' | 'right';
      onClick: () => void;
    }
>;

export const MenuItem: FC<MenuItemProps<unknown>> = ({
  className,
  onClick,
  icon,
  label,
  children,
  subtitle,
  disabled,
  autofocus,
  type = 'button',
  isSelected = false,
  textAlign = 'left',
}) => {
  if (type === 'checkbox' || type === 'radio') {
    icon = isSelected ? 'check' : 'blank';
  }
  return (
    <li aria-disabled={!!disabled}>
      <button
        className={cn(className, styles.item, textAlign === 'right' && styles.textAlignRight)}
        disabled={!!disabled}
        onClick={onClick}
        onMouseMove={ev => {
          if (!ev.currentTarget.contains(document.activeElement)) {
            ev.currentTarget.focus();
          }
        }}
        data-autofocus={!!autofocus}
        onPointerDown={ev => {
          // For Safari... force focus on mousedown
          const ct = ev.currentTarget;
          setTimeout(() => ct.focus());
        }}
        onKeyDown={ev => {
          if (ev.code === 'ArrowUp' || ev.code === 'ArrowDown') {
            const next = ev.code === 'ArrowDown';
            const li = ev.currentTarget.closest('li')!;
            const items = Array.from(
              ev.currentTarget!.closest('ul')!.querySelectorAll('li:not([disabled])')
            );
            const idx = (items.indexOf(li) + (next ? 1 : -1) + items.length) % items.length;
            items[idx]?.querySelector('button')?.focus();
            ev.preventDefault();
          }
        }}
      >
        {icon && <Icon icon={icon} />}
        <div className={styles.label}>{label || children}</div>
        {subtitle && <div className={styles.subtitle}>{subtitle}</div>}
      </button>
    </li>
  );
};

export const MenuDivider = () => <hr />;
