import cn from 'classnames';
import React, {useEffect, useState} from 'react';
import {createRoot} from 'react-dom/client';
import {BackendPorts} from './components/mainpanel/BackendPorts';
import {IntegrationsSection} from './components/mainpanel/IntegrationsSection';
import {OriginalMainPanel} from './components/mainpanel/OriginalMainPanel';
import {ReleaseNotesBanner} from './components/mainpanel/ReleaseNotesBanner';
import {TopNavRail} from './components/mainpanel/TopNavRail';
import {WorkspaceInfoTile} from './components/mainpanel/WorkspaceInfoTile';
import {createHtmlBroker} from './globals/html-broker';
import {setupVsCodeHelperIframe} from './mainpanel-vscode-helper';
import styles from './mainpanel.entry.scss';

const vscode = (window as any)['acquireVsCodeApi']();
export const broker = createHtmlBroker(vscode);

function MainPanel() {
  // eventually remove this once new main panel is no longer an experiment
  const [newMainPanel, setNewMainPanel] = useState<boolean | undefined>();

  useEffect(() => {
    broker.send('isNewUIEnabled');
    broker.on('notifyIsNewUIEnabled', isEnabled => {
      setNewMainPanel(isEnabled);
    });
  }, []);

  return (
    <div className={cn(styles.root)}>
      {newMainPanel !== undefined && (
        <>
          {newMainPanel ? (
            <>
              <TopNavRail />
              <ReleaseNotesBanner />
              <WorkspaceInfoTile />
              <IntegrationsSection />
              <BackendPorts />
            </>
          ) : (
            <OriginalMainPanel />
          )}
        </>
      )}
    </div>
  );
}

const root = createRoot(document.getElementById('root')!);
root.render(<MainPanel />);

setupVsCodeHelperIframe(vscode);
