import {VSCodeDivider, VSCodeTag, VSCodeProgressRing} from '@vscode/webview-ui-toolkit/react';
import cn from 'classnames';
import React, {useState} from 'react';
import {Icon, IconName} from 'react-shared/components/Icon';
import {MenuIconButton} from 'react-shared/components/MenuIconButton';
import {MenuItem} from 'react-shared/components/PopupMenu';
import {Body, Label} from 'react-shared/components/Text';
import {
  Integration,
  IntegrationId,
  Widget,
  WidgetId,
} from '~common/messaging/modules/main-panel/integrations';
import {broker} from '~webviews/mainpanel.entry';
import styles from './IntegrationCard.scss';
import {Expando} from 'react-shared/components/Expando';

export function CollapsibleIntegrationCard({
  iconUrl,
  integrationName,
  title,
  children,
  id,
  defaultExpanded,
}: React.PropsWithChildren<{
  iconUrl: string;
  integrationName: string;
  title?: string | null;
  id: IntegrationId;
  defaultExpanded: boolean | undefined;
}>) {
  const [isExpanded, setIsExpanded] = useState(defaultExpanded ?? true);

  return (
    <div className={styles.addedIntegrationCardContainer}>
      <div className={styles.card}>
        <button
          onClick={() => {
            setIsExpanded(!isExpanded);
            broker.send('toggledExpanded', id, !isExpanded);
          }}
          className={cn(styles.cardHeaderButton, {[styles.cardIsOpen]: isExpanded})}
        >
          <div className={styles.leftSideOfHeader}>
            <div className={styles.headerIconArea}>
              <MenuIconButton
                className={styles.optionsButton}
                icon="kebab-vertical"
                tooltip="Options"
                onClick={ev => {
                  ev.stopPropagation();
                }}
              >
                <MenuItem
                  onClick={() => {
                    broker.send('toggleIntegrationAdded', id);
                  }}
                  label="Remove"
                />
              </MenuIconButton>
              <img src={iconUrl} width={16} height={16} className={styles.cardIcon} />
            </div>
            <div>
              {title && <span className={styles.cardName}>{integrationName}</span>}
              <Body noCursor level={1} className={styles.cardHeading}>
                {title ?? integrationName}
              </Body>
            </div>
          </div>
          <Icon icon="chevron-down" className={styles.chevronIcon} />
        </button>
        <Expando expanded={isExpanded} classNameContent={styles.cardContent}>
          <CardDivider />
          {children}
        </Expando>
      </div>
    </div>
  );
}

export function CardDivider() {
  return <VSCodeDivider className={styles.cardDivider} />;
}

export function widgetFactory(integrationId: IntegrationId) {
  return function (widget: Widget & {id: WidgetId}) {
    const showLoading = widget.loading && !widget.hideLoading;

    return (
      <div key={widget.id} className={styles.linkContainer}>
        {showLoading ? (
          <VSCodeProgressRing className={styles.itemIcon} />
        ) : (
          <Icon icon={widget.icon} className={styles.itemIcon} />
        )}
        <div>
          <a
            href="#"
            onClick={() =>
              !widget.loading && broker.send('runIntegrationWidget', integrationId, widget.id)
            }
            className={cn({
              [styles.widgetLoading]: showLoading,
            })}
          >
            {widget.title}
          </a>
          {widget.subtitle && <span className={styles.subtitle}>{widget.subtitle}</span>}
        </div>
      </div>
    );
  };
}

export function CardItem({icon, text}: {icon: IconName; text: string}) {
  return (
    <div className={styles.linkContainer}>
      <Icon icon={icon} className={styles.itemIcon} />
      <Body className={styles.itemText}>{text}</Body>
    </div>
  );
}

type UnaddedIntegrationCardProps = Pick<
  Integration,
  'icon' | 'addCtaTitle' | 'description' | 'label' | 'id'
> & {
  onIntegrationAdded?: () => void;
};
export function UnaddedIntegrationCard({
  icon,
  addCtaTitle,
  description,
  label,
  id,
  onIntegrationAdded,
}: UnaddedIntegrationCardProps) {
  return (
    <div className={styles.unaddedIntegrationCardContainer}>
      <button
        onClick={() => {
          broker.send('toggleIntegrationAdded', id);
          onIntegrationAdded?.();
        }}
        className={styles.integrationCard}
      >
        <img src={icon} width={16} height={16} className={styles.cardIcon} />
        <div className={styles.cardBody}>
          <Body noCursor level={1} className={styles.cardHeading}>
            {addCtaTitle}
          </Body>
          <Label noCursor level={3} className={styles.cardText}>
            {description}
          </Label>
        </div>
        {label ? <VSCodeTag className={styles.cardTag}>{label}</VSCodeTag> : null}
      </button>
    </div>
  );
}
