import cn from 'classnames';
import React, {useEffect, useRef, useState} from 'react';
import {Icon, IconName} from 'react-shared/components/Icon';
import {MenuDivider, MenuItem, MenuState, PopupMenu} from 'react-shared/components/PopupMenu';
import {Body, Label} from 'react-shared/components/Text';
import {TroubleshootingInfo} from '~common/messaging/modules/main-panel/main-panel-protocol';
import {broker} from '~webviews/mainpanel.entry';
import styles from './TopNavRail.scss';

export function TopNavRail({className}: {className?: string}) {
  const [troubleshootingInfo, setTroubleshootingInfo] = useState<TroubleshootingInfo | null>(null);

  useEffect(() => {
    broker.send('getTroubleshootingInfo');
    broker.on('notifyTroubleshootingInfo', (info: TroubleshootingInfo) => {
      setTroubleshootingInfo(info);
    });
  }, []);

  return (
    <div className={cn(className, styles.rail)}>
      <RailItem icon="mono-home" label="Home" onClick={() => broker.send('navigateToDashboard')} />
      <RailItem
        icon="list-selection"
        label="Docs"
        onClick={() => broker.send('openUrl', 'https://developers.google.com/idx/')}
      />
      <MenuRailItem icon="compass" label="Support" dropdownSide="left">
        {troubleshootingInfo && (
          <TroubleshootingInfoMenuItem troubleshootingInfo={troubleshootingInfo} />
        )}
        <MenuDivider />
        <MenuItem
          onClick={() => broker.send('openUrl', 'https://developers.google.com/idx/support/help')}
        >
          Support Home
        </MenuItem>
        <MenuItem onClick={() => broker.send('navigateToTroubleshootingGuide')}>
          Troubleshooting Guide
        </MenuItem>
        <MenuDivider />
        <MenuItem onClick={() => broker.send('openUrl', 'https://community.idx.dev')}>
          Ask a Question...
        </MenuItem>
        <MenuItem onClick={() => broker.send('navigateToFileABug')}>File a Bug...</MenuItem>
        <MenuItem onClick={() => broker.send('navigateToFileAFeatureRequest')}>
          File a Feature Request...
        </MenuItem>
        <MenuDivider />
        <MenuItem onClick={() => broker.send('navigateToSeeRoadmap')}>See Roadmap</MenuItem>
      </MenuRailItem>
      <MenuRailItem icon="menu" label="Shortcuts" dropdownSide="left">
        <MenuItem onClick={() => broker.send('openMonospaceCommands')}>
          Project IDX Commands
        </MenuItem>
        <MenuItem onClick={() => broker.send('openMonospaceSettings')}>
          Project IDX Settings
        </MenuItem>
        <MenuDivider />
        <MenuItem onClick={() => broker.send('navigateToFullReleaseNotes')}>Release Notes</MenuItem>
      </MenuRailItem>
    </div>
  );
}

function TroubleshootingInfoMenuItem({
  troubleshootingInfo,
}: {
  troubleshootingInfo: TroubleshootingInfo;
}) {
  let {containerCommit, workspaceId, permanentTag} = troubleshootingInfo;
  return (
    <div
      tabIndex={0}
      onClick={ev => {
        ev.stopPropagation();
      }}
      className={styles.troubleshootingInfo}
    >
      {workspaceId.length > 0 && (
        <dl>
          <Body level={2} as="dt" secondary>
            Workspace ID:
          </Body>
          <Body level={2} as="dd" selectable>
            {workspaceId}
          </Body>
        </dl>
      )}

      {containerCommit.length > 0 && (
        <dl>
          <Body level={2} as="dt" secondary>
            Container Commit:
          </Body>
          <Body level={2} as="dd" selectable>
            {containerCommit}
          </Body>
        </dl>
      )}

      {permanentTag.length > 0 && (
        <dl>
          <Body level={2} as="dt" secondary>
            Permanent Tag:
          </Body>
          <Body level={2} as="dd" selectable>
            {permanentTag}
          </Body>
        </dl>
      )}
    </div>
  );
}

interface RailItemProps {
  label: string;
  icon: IconName;
  className?: string;
  onClick: React.MouseEventHandler<HTMLButtonElement>;
}

const RailItem = React.forwardRef<HTMLButtonElement, RailItemProps>(
  ({icon, label, className, onClick}, ref) => {
    return (
      <button ref={ref} className={cn(className, styles.item)} onClick={onClick}>
        <Icon icon={icon} />
        <Label noCursor level={4}>
          {label}
        </Label>
      </button>
    );
  }
);

type MenuRailItemProps = Omit<RailItemProps, 'onClick'> &
  React.PropsWithChildren<{
    dropdownSide?: 'left' | 'right';
    minimizeWidth?: boolean;
  }>;

function MenuRailItem({
  dropdownSide = 'right',
  minimizeWidth,
  children,
  ...props
}: MenuRailItemProps) {
  const [menuState, setMenuState] = useState<MenuState | null>(null);
  const buttonRef = useRef<HTMLButtonElement | null>(null);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    if (!buttonRef.current || !isOpen) {
      return;
    }
    const buttonRect = buttonRef.current.getBoundingClientRect();
    const oppositeSide = dropdownSide === 'right' ? 'left' : 'right';
    if (dropdownSide === 'left') {
      setMenuState({
        top: buttonRect.bottom + 4,
        right: window.innerWidth - buttonRect[oppositeSide],
      });
    } else {
      setMenuState({
        top: buttonRect.bottom + 4,
        left: buttonRect[oppositeSide],
      });
    }
  }, [isOpen, dropdownSide]);

  return (
    <>
      <RailItem ref={buttonRef} onClick={() => setIsOpen(true)} {...props} />
      <PopupMenu
        show={isOpen}
        left={menuState?.left}
        top={menuState?.top || 100}
        right={menuState?.right}
        minimizeWidth={minimizeWidth}
        onClose={() => setIsOpen(false)}
      >
        {children}
      </PopupMenu>
    </>
  );
}
