import cn from 'classnames';
import React, {useEffect, useReducer, useState} from 'react';
import ReactMarkdown from 'react-markdown';
import styles from './OriginalMainPanel.scss';
import {
  PortInfo,
  TroubleshootingInfo,
} from '~common/messaging/modules/main-panel/main-panel-protocol';
import {VSCodeBadge, VSCodeButton, VSCodeDivider} from '@vscode/webview-ui-toolkit/react';
import {Body, Heading} from 'react-shared/components/Text';
import {Spacer} from 'react-shared/components/Spacer';
import {Icon} from 'react-shared/components/Icon';
import {MenuItem, MenuState, PopupMenu} from 'react-shared/components/PopupMenu';
import {IconButton} from 'react-shared/components/IconButton';
import {broker} from '~webviews/mainpanel.entry';

export function OriginalMainPanel() {
  const [troubleshootingInfo, setTroubleshootingInfo] = useState<TroubleshootingInfo | null>(null);
  const [feedbackMenuState, setFeedbackMenuState] = useState<MenuState | null>(null);
  const [releaseNotes, setReleaseNotes] = useState<string | null>(null);
  const [backendPorts, setBackendPorts] = useState<PortInfo[] | null>(null);
  const [troubleshootKey, incrementTroubleshootKey] = useReducer(key => key + 1, 0);

  useEffect(() => {
    broker.send('getTroubleshootingInfo');
    broker.send('getReleaseNotes');

    broker.on('notifyTroubleshootingInfo', (info: TroubleshootingInfo) => {
      setTroubleshootingInfo(info);
    });

    broker.on('notifyReleaseNotes', (notes: string) => {
      setReleaseNotes(notes);
    });

    broker.on('backendPortUpdate', (ports: PortInfo[]) => {
      setBackendPorts(ports);
    });

    broker.on('flashTroubleshootSection', incrementTroubleshootKey);
  }, []);

  return (
    <div className={styles.originalMainPanel}>
      <Body secondary level={1}>
        Welcome to Project IDX, an experimental web-based development workspace from Google.{' '}
        <span className={styles.error}>
          You're in the public, limited preview program for Project IDX.
        </span>
      </Body>
      <Spacer size="large" />

      <VSCodeButton appearance="secondary" onClick={() => broker.send('navigateToDocs')}>
        <Icon className={styles.buttonLeftIcon} icon="book" />
        View Docs
      </VSCodeButton>
      <Spacer size="large" />

      <VSCodeButton appearance="secondary" onClick={() => broker.send('navigateToDashboard')}>
        <Icon className={styles.buttonLeftIcon} icon="home" />
        Open Dashboard
      </VSCodeButton>
      <Spacer size="large" />

      <VSCodeDivider />
      <Spacer size="large" />

      <Heading level={5}>Quick Access</Heading>
      <Spacer size="large" />

      <VSCodeButton appearance="secondary" onClick={() => broker.send('openShareWorkspace')}>
        <Icon className={styles.buttonLeftIcon} icon="mono-share" />
        Share Workspace
      </VSCodeButton>
      <Spacer size="large" />

      <VSCodeButton appearance="secondary" onClick={() => broker.send('openMonospaceCommands')}>
        <Icon className={styles.buttonLeftIcon} icon="arrow-circle-right" />
        Project IDX Commands
      </VSCodeButton>
      <Spacer size="large" />

      <VSCodeButton appearance="secondary" onClick={() => broker.send('openMonospaceSettings')}>
        <Icon className={styles.buttonLeftIcon} icon="gear" />
        Project IDX Settings
      </VSCodeButton>
      <Spacer size="large" />

      <VSCodeDivider />
      <Spacer size="large" />

      <Heading level={5}>Troubleshooting</Heading>
      <Spacer size="large" />

      {!troubleshootingInfo && <>Loading...</>}
      {troubleshootingInfo && (
        <div className={cn(styles.troubleshootingInfoGroup)}>
          {troubleshootingInfo.workspaceId.length > 0 && (
            <dl className={cn(styles.troubleshootingInfoWrapper)}>
              <Body level={2} as="dt" secondary>
                Workspace ID:
              </Body>
              <Body level={2} as="dd" className={cn(styles.troubleshootingInfoValue)} selectable>
                {troubleshootingInfo.workspaceId}
              </Body>
            </dl>
          )}

          <dl className={cn(styles.troubleshootingInfoWrapper)}>
            <Body level={2} as="dt" secondary>
              IDX version:
            </Body>
            <Body level={2} as="dd" className={cn(styles.troubleshootingInfoValue)} selectable>
              {troubleshootingInfo.monospaceVersion}
            </Body>
          </dl>

          {troubleshootingInfo.containerCommit.length > 0 && (
            <dl className={cn(styles.troubleshootingInfoWrapper)}>
              <Body level={2} as="dt" secondary>
                Container Commit:
              </Body>
              <Body level={2} as="dd" className={cn(styles.troubleshootingInfoValue)} selectable>
                {troubleshootingInfo.containerCommit}
              </Body>
            </dl>
          )}

          {troubleshootingInfo.permanentTag.length > 0 && (
            <dl className={cn(styles.troubleshootingInfoWrapper)}>
              <Body level={2} as="dt" secondary>
                Permanent Tag:
              </Body>
              <Body level={2} as="dd" className={cn(styles.troubleshootingInfoValue)} selectable>
                {troubleshootingInfo.permanentTag}
              </Body>
            </dl>
          )}
        </div>
      )}

      <Spacer size="large" />
      <VSCodeButton
        appearance="secondary"
        onClick={() => broker.send('navigateToTroubleshootingGuide')}
      >
        <Icon className={styles.buttonLeftIcon} icon="bug" />
        Troubleshooting Guide
      </VSCodeButton>
      <Spacer size="large" />

      <div
        className={cn(styles.feedbackGroup, {[styles.highlightFlash]: troubleshootKey !== 0})}
        key={troubleshootKey}
      >
        <VSCodeButton
          appearance="secondary"
          onClick={ev => {
            const v = ev.currentTarget.getBoundingClientRect();
            setFeedbackMenuState({
              left: v.left,
              top: v.top + v.height + 4,
            });
          }}
        >
          <Icon className={styles.buttonLeftIcon} icon="feedback" />
          Send Feedback
        </VSCodeButton>
        <PopupMenu
          show={!!feedbackMenuState}
          onClose={() => setFeedbackMenuState(null)}
          className={cn(styles.feedbackMenu)}
          left={feedbackMenuState?.left || 0}
          top={feedbackMenuState?.top || 0}
        >
          <MenuItem onClick={() => broker.send('navigateToFileABug')}>File a Bug...</MenuItem>
          <MenuItem onClick={() => broker.send('navigateToFileAFeatureRequest')}>
            File a Feature Request...
          </MenuItem>
          <Spacer size="small" />
          <VSCodeDivider />
          <Spacer size="small" />
          <MenuItem onClick={() => broker.send('navigateToSeeRoadmap')}>See Roadmap</MenuItem>
        </PopupMenu>
      </div>

      <Spacer size="large" />
      <VSCodeDivider />
      <Spacer size="large" />

      {releaseNotes === null && <>Loading...</>}
      {releaseNotes && releaseNotes?.length > 0 && (
        <>
          <Heading level={5}>What's New</Heading>
          <Spacer size="large" />

          <ReactMarkdown
            components={{
              h1: ({node, ...props}) => <Body {...props} as="h6" level={2} secondary />,
              h2: ({node, ...props}) => <Body {...props} as="h6" level={2} secondary />,
              h3: ({node, ...props}) => <Body {...props} as="h6" level={2} secondary />,
              h4: ({node, ...props}) => <Body {...props} as="h6" level={2} secondary />,
              p: ({node, ...props}) => <Body {...props} as="p" level={2} secondary />,
              li: ({node, ...props}) => <Body {...props} as="li" level={2} secondary />,
              ul: ({node, ...props}) => <ul {...props} className={styles.markdownUl} />,
              ol: ({node, ...props}) => <ul {...props} className={styles.markdownOl} />,
            }}
          >
            {releaseNotes}
          </ReactMarkdown>

          <Spacer size="large" />
          <VSCodeButton
            appearance="secondary"
            onClick={() => broker.send('navigateToFullReleaseNotes')}
          >
            <Icon className={styles.buttonLeftIcon} icon="megaphone" />
            Full Release Notes
          </VSCodeButton>
        </>
      )}
      <Spacer size="large" />
      <VSCodeDivider />
      <Spacer size="large" />
      <>
        <Heading className={styles.headingWithBadge} level={5}>
          Backend Ports
          {!!backendPorts?.length && <VSCodeBadge>{backendPorts?.length}</VSCodeBadge>}
        </Heading>
        <Spacer size="large" />
        <Body secondary level={1}>
          To access backend servers from web frontends, use the mapped URLs below. Mapped URLs are
          in the form [port]-$WEB_HOST.{' '}
          <a href="javascript:null" onClick={() => broker.send('navigateToSeeWebHostDocs')}>
            Documentation
          </a>
        </Body>
        {!!backendPorts?.length && (
          <Body secondary level={1}>
            <div className={cn(styles.table)}>
              <div className={cn(styles.tableRow, styles.tableHeader)}>
                <div className={cn(styles.tableRowItem, styles.columnPort)}>Port</div>
                <div className={cn(styles.tableRowItem, styles.columnPid)}>PID</div>
                <div className={cn(styles.tableRowItem)}>Mapped URL</div>
              </div>
              <>
                {backendPorts.map(({port, url, pid}) => (
                  <>
                    <div className={cn(styles.tableRow)}>
                      <div
                        className={cn(styles.tableRowItem, styles.columnPort, styles.selectable)}
                      >
                        {port}
                      </div>
                      <div className={cn(styles.tableRowItem, styles.columnPid, styles.selectable)}>
                        {pid || '--'}
                      </div>
                      <div className={cn(styles.tableRowItem, styles.copyParent)}>
                        <span className={cn(styles.ellipsis)} title={`https://${url}`}>
                          {url}
                        </span>
                        <IconButton
                          className={styles.copyButton}
                          tooltip="Open in a new window"
                          icon="link-external"
                          onClick={() => broker.send('openUrl', `https://${url}`)}
                        />
                        <IconButton
                          className={styles.copyButton}
                          tooltip="Copy to clipboard"
                          icon="copy"
                          onClick={() => navigator.clipboard.writeText(url)}
                        />
                      </div>
                    </div>
                  </>
                ))}
              </>
            </div>
          </Body>
        )}
        {!backendPorts?.length && (
          <Body className={styles.emptyMessage} secondary level={2}>
            No running servers found
          </Body>
        )}
      </>
    </div>
  );
}
