import React from 'react';
import {Integration} from '~common/messaging/modules/main-panel/integrations';
import {
  CardDivider,
  CardItem,
  CollapsibleIntegrationCard,
  widgetFactory,
} from '../IntegrationCards';

const Widget = widgetFactory('firebase_hosting');

export function FirebaseIntegration({integration}: {integration: Integration<'firebase_hosting'>}) {
  return (
    <CollapsibleIntegrationCard
      id={integration.id}
      iconUrl={integration.icon}
      integrationName={integration.title}
      defaultExpanded={integration.state.expanded}
      title={integration.state.projectId}
    >
      {integration.state.authedEmail ? (
        <CardItem icon="account" text={integration.state.authedEmail} />
      ) : (
        <Widget id="login" {...integration.widgets.login} />
      )}
      {integration.state.deploymentHostingUrl && (
        <>
          <Widget
            id="open_app_hosting"
            {...integration.widgets.open_app_hosting}
            title={integration.state.deploymentHostingUrl}
          />
        </>
      )}
      {integration.state.projectId ? (
        <>
          <Widget id="deploy_to_prod" {...integration.widgets.deploy_to_prod} />
          <Widget id="deploy_to_channel" {...integration.widgets.deploy_to_channel} />
        </>
      ) : (
        <Widget id="initialize" {...integration.widgets.initialize} />
      )}

      <CardDivider />
      <Widget id="open_docs" {...integration.widgets.open_docs} />
      {integration.state.showTryAppHosting && (
        <>
          <CardDivider />
          <Widget id="try_app_hosting" {...integration.widgets.try_app_hosting} />
        </>
      )}
    </CollapsibleIntegrationCard>
  );
}
