import {VSCodeButton} from '@vscode/webview-ui-toolkit/react';
import React from 'react';
import {Integration} from '~common/messaging/modules/main-panel/integrations';
import {broker} from '~webviews/mainpanel.entry';
import {CardItem, CollapsibleIntegrationCard, widgetFactory} from '../IntegrationCards';

const Widget = widgetFactory('cloud_run_deploy');

export function CloudRunIntegration({integration}: {integration: Integration<'cloud_run_deploy'>}) {
  return (
    <CollapsibleIntegrationCard
      iconUrl={integration.icon}
      integrationName={integration.title}
      id={integration.id}
      title={integration.state.projectId}
      defaultExpanded={integration.state.expanded}
    >
      {integration.state.authenticated ? (
        <>
          {integration.state.enabled ? (
            <>
              {integration.state.lastDeployment ? (
                <>
                  <Widget
                    id="open_cloud_run_app"
                    {...integration.widgets.open_cloud_run_app}
                    title={integration.state.lastDeployment.serviceName}
                    subtitle={`Last deployed on ${integration.state.lastDeployment.lastUpdated}`}
                  />
                  <Widget id="set_service_env_var" {...integration.widgets.set_service_env_var} />
                  <Widget id="open_dashboard" {...integration.widgets.open_dashboard} />
                </>
              ) : (
                <CardItem icon="globe" text="Not yet deployed" />
              )}

              <VSCodeButton
                onClick={() => {
                  broker.send('runIntegrationWidget', integration.id, 'deploy');
                }}
                disabled={integration.widgets.deploy.loading}
              >
                {integration.widgets.deploy.loading ? 'Deploying...' : 'Deploy'}
              </VSCodeButton>
            </>
          ) : (
            <Widget id="enable_api" {...integration.widgets.enable_api} />
          )}
        </>
      ) : (
        <Widget id="run_oauth" {...integration.widgets.run_oauth} />
      )}
    </CollapsibleIntegrationCard>
  );
}
