import cn from 'classnames';
import React, {useEffect, useState} from 'react';
import {IconButton} from 'react-shared/components/IconButton';
import {Label} from 'react-shared/components/Text';
import {WorkspaceMetadata} from 'vscode-shared/vscodeplugin-exports';
import {TroubleshootingInfo} from '~common/messaging/modules/main-panel/main-panel-protocol';
import {broker} from '~webviews/mainpanel.entry';
import styles from './WorkspaceInfoTile.scss';

export function WorkspaceInfoTile({className}: {className?: string}) {
  const [workspaceMetadata, setWorkspaceMetadata] = useState<WorkspaceMetadata | undefined>();
  const [troubleshootingInfo, setTroubleshootingInfo] = useState<TroubleshootingInfo | null>(null);

  useEffect(() => {
    broker.send('getTroubleshootingInfo');
    broker.on('notifyTroubleshootingInfo', (info: TroubleshootingInfo) => {
      setTroubleshootingInfo(info);
    });

    broker.send('getWorkspaceMetadata');
    broker.on('notifyWorkspaceMetadata', workspaceMetadata => {
      setWorkspaceMetadata(workspaceMetadata);
    });
  }, []);

  let {workspaceIconUrl, workspaceName} = workspaceMetadata || {};
  let {workspaceId} = troubleshootingInfo || {};

  return (
    <div className={cn(className, styles.tile)}>
      {workspaceIconUrl && <img src={workspaceIconUrl} className={styles.icon} />}
      <div className={styles.info}>
        <Label level={1}>{workspaceName || 'Your Workspace'}</Label>
        <Label selectable level={2} secondary className={styles.workspaceId}>
          {workspaceId}
        </Label>
      </div>
      <div className={styles.actions}>
        <IconButton
          icon="mono-share"
          tooltip="Share Workspace"
          onClick={() => broker.send('openShareWorkspace')}
        />
      </div>
    </div>
  );
}
